import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AppBannerComponent } from './app-banner.component';
import { AppBanneraddComponent } from './app-banneradd.component';
import { AppBannereditComponent } from './app-banneredit.component';


const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Daftar banner'
    },
    children: [
      {
        path: '',
        component: AppBannerComponent,
      },
      {
        path: 'app-banneradd',
        component: AppBanneraddComponent,
        data: {
          title: 'Tambah banner'
        }
      },
      {
        path: 'app-banneredit',
        component: AppBannereditComponent,
        data: {
          title: 'Ubah banner'
        }
      }
      ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AppBannerRoutingModule { }
