import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../api.service';
import { HttpClient, HttpEventType } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { NgxSpinnerService } from "ngx-spinner";
import { Router } from "@angular/router";

@Component({
  selector: 'app-app-banneradd',
  templateUrl: './app-banneradd.component.html',
  styleUrls: ['./app-banneradd.component.css']
})
export class AppBanneraddComponent implements OnInit {
	banner: File[] = [];
	files: File[] = [];
	itemBanner: File = null;
	itemFiles: File = null;
	isSuccessAlert: any = false;
	isDangerAlert: any = false;

	banner_name: any = '';
	banner_place: any = '';
	banner_link: any = '';
	banner_is_page: any = false;
	banner_start_date: any = '';
	banner_end_date: any = '';

  constructor(
  	public api: ApiService,
  	public http: HttpClient,
  	public spinner: NgxSpinnerService,
  	public router: Router
  	) { }

  ngOnInit(): void {
  }

  onSelect(event, type) {
		if(type=='files'){
			// console.log(event);
	  		this.files.push(...event.addedFiles);
	  		console.log(this.files);
	  		this.itemFiles = this.files[0];
		}
		else if(type=='banner'){
			console.log(event);
	  		this.banner.push(...event.addedFiles);
	  		this.itemBanner = this.banner[0];
	  		 // const formData = new FormData();
    
	     //    for (var i = 0; i < this.banner.length; i++) { 
	     //      formData.append("banner[]", this.banner[i]);
	     //    }
	     //    formData.append("param", "banner");
	   
	     //    this.http.post(this.api.uploadUrl+'upload.php', formData)
	     //    .subscribe(res => {
	     //       console.log(res);
	     //       alert('Uploaded Successfully.');
	     //    })
		}
	  
	}
	 
	onRemove(event, type) {
		if(type=='files'){
			console.log(event);
	  		this.files.splice(this.files.indexOf(event), 1);
		}
		else if(type=='banner'){
			console.log(event);
	  		this.banner.splice(this.banner.indexOf(event), 1);
		}
	  
	}

	saveProgram(){
		this.spinner.show();
		
	  	const fd = new FormData();
	  	fd.append('param', 'create');
	  	fd.append('banner_name', this.banner_name);
	  	fd.append('banner_link', this.banner_link);
	  	fd.append('banner_is_page', this.banner_is_page);
	  	fd.append('banner_place', this.banner_place);
	  	fd.append('banner_start_date', this.banner_start_date);
	  	fd.append('banner_end_date', this.banner_end_date);
	  	fd.append('banner[]', this.itemBanner);

	    this.http.post(this.api.uploadUrl+'upload-banner.php', fd)
		        .subscribe(res => {
		        this.spinner.hide();
		           console.log(res);
		           	this.isSuccessAlert = true;
		           	this.banner_name = '';
					this.banner_place = '';
					this.banner_link = '';
					this.banner_is_page = false;
					this.banner_start_date = '';
					this.banner_end_date = '';
					this.banner = [];

					setTimeout(()=>{ 
					    this.isSuccessAlert = false;
					 }, 3000);
		           // alert('Uploaded Successfully.');
		        });
	}

}
